.watch-main {
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.watch {
  box-sizing: border-box;
  background-color: #fff;
  border: 10px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
}

.watch__stop {
  background-color: #ff0;
  border: 1px;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  position: absolute;
  top: calc(50% - 15px);
  left: calc(50% - 15px);
}

.watch__hands {
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  position: fixed;
}

.watch__hands--minute {
  z-index: 0;
  background-color: #00f;
  width: 20px;
  height: 20vmin;
  animation: 60s linear infinite clock;
  top: calc(50vh - 20vmin);
  left: calc(50% - 10px);
}

.watch__hands--second {
  background-color: #7fffd4;
  width: 10px;
  height: 40vmin;
  animation: 5s linear infinite clock;
  top: calc(50vh - 40vmin);
  left: calc(50% - 5px);
}

.watch__hands:hover {
  animation-play-state: paused;
}

@keyframes clock {
  0% {
    transform-origin: bottom;
    transform: rotate(0);
  }

  100% {
    transform-origin: bottom;
    transform: rotate(360deg);
  }
}

